.container {
    width: 90%;
    background: #efefef;
    margin: 10px 0;
    display: flex;
    justify-content: space-between;
}

.image img {
    width: 100px;
    height: 100px;
}

.image {
    grid-area: im;
    display: flex;
    flex-direction: column;
}

.name {
    grid-area: n;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    padding: 20px;
}

.location {
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
}

.on {
    background: #00ff00;
}

.off {
    background: red;
}

.pageActive {
    font-weight: bold;
}

.pageNoActive {
    font-weight: 200;
}

.nav span{
    padding: 0 10px;
    cursor: pointer;
}

.imageAnimation {
    animation-duration: 3s;
    animation-name: logoRotate;
}

@keyframes logoRotate {
    0% {
    transform: rotate(0deg);
}  50% {
    transform: rotate(180deg);
}  100% {
    transform: rotate(360deg);
}
}


